// src/pages/PreviewPage.tsx
import React, { useEffect, useMemo, useState } from 'react';
import {
  Container, Typography, TextField, Button, MenuItem, FormControl,
  InputLabel, Select, FormControlLabel, RadioGroup, Radio,
  Checkbox, FormHelperText, Box, Alert, Snackbar
} from '@mui/material';
import { useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { loadFormsFromLocalStorage } from '../store/formSlice';
import { RootState } from '../store/store';
import { FormSchema, FormField } from '../types/form';

type ValuesMap = Record<string, any>;
type ErrorsMap = Record<string, string | null>;

export default function PreviewPage() {
  const { id } = useParams<{ id: string }>();
  const dispatch = useDispatch();
  const { formsList, currentForm } = useSelector((state: RootState) => state.form);

  // Load forms from localStorage when page loads
  useEffect(() => {
    dispatch(loadFormsFromLocalStorage());
  }, [dispatch]);

  // Find the form in Redux store OR fallback to currentForm
  const form: FormSchema | undefined = useMemo(() => {
    if (!id) return undefined;
    return formsList.find((f) => f.id === id) || (currentForm.id === id ? currentForm : undefined);
  }, [formsList, id, currentForm]);

  // Initialize field values
  const [values, setValues] = useState<ValuesMap>({});
  const [errors, setErrors] = useState<ErrorsMap>({});
  const [alert, setAlert] = useState<{ open: boolean; severity: 'success' | 'error'; message: string }>({
    open: false,
    severity: 'success',
    message: '',
  });

  // When form changes, set default values
  useEffect(() => {
    if (form) {
      const initial: ValuesMap = {};
      form.fields.forEach((f) => {
        if (f.defaultValue !== undefined) initial[f.id] = f.defaultValue;
        else if (f.type === 'checkbox') initial[f.id] = false;
        else initial[f.id] = '';
      });
      setValues(initial);
    }
  }, [form]);

  // Validation helpers
  const isEmail = (s: string) => /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(s);
  const hasNumber = (s: string) => /\d/.test(s);

  const validateField = (field: FormField, val: any): string | null => {
    const rules = field.validations;
    if (!rules) return null;
    if (rules.required) {
      const empty =
        val === undefined ||
        val === null ||
        val === '' ||
        (field.type === 'checkbox' && val === false);
      if (empty) return 'This field is required';
    }
    if (typeof val === 'string') {
      if (rules.minLength && val.length < rules.minLength) return `Minimum length ${rules.minLength}`;
      if (rules.maxLength && val.length > rules.maxLength) return `Maximum length ${rules.maxLength}`;
    }
    if (rules.email && val && !isEmail(val)) return 'Invalid email';
    if (rules.passwordRule && typeof val === 'string') {
      if (val.length < 8) return 'Password must be at least 8 characters';
      if (!hasNumber(val)) return 'Password must contain at least one number';
    }
    return null;
  };

  // Derived field evaluation
  const evaluateDerived = (formula: string, currentValues: ValuesMap) => {
    const replaced = formula.replace(/{{\s*([^}\s]+)\s*}}/g, (_, id) =>
      JSON.stringify(currentValues[id] ?? '')
    );
    try {
      // eslint-disable-next-line no-new-func
      return Function(`return (${replaced});`)();
    } catch {
      return '';
    }
  };

  // Update derived fields automatically
  useEffect(() => {
    if (!form) return;
    const updated = { ...values };
    form.fields.forEach((f) => {
      if (f.derived?.formula) {
        updated[f.id] = evaluateDerived(f.derived.formula, updated);
      }
    });
    setValues(updated);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [JSON.stringify(values), form?.fields]);

  if (!form) {
    return (
      <Container>
        <Typography variant="h5" gutterBottom>
          Form not found
        </Typography>
        <Typography variant="body2">Go to /myforms and select a saved form.</Typography>
      </Container>
    );
  }

  // Change handler
  const handleChange = (id: string, val: any) => {
    setValues((prev) => ({ ...prev, [id]: val }));
    setErrors((prev) => ({ ...prev, [id]: null }));
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const nextErrors: ErrorsMap = {};
    let hasError = false;
    form.fields.forEach((f) => {
      if (f.derived) return; // skip derived field validation
      const err = validateField(f, values[f.id]);
      if (err) {
        nextErrors[f.id] = err;
        hasError = true;
      }
    });
    setErrors(nextErrors);
    if (hasError) {
      setAlert({ open: true, severity: 'error', message: 'Please fix errors before submitting' });
    } else {
      setAlert({ open: true, severity: 'success', message: 'Form submitted successfully' });
      console.log('Submitted values:', values);
    }
  };

  return (
    <Container>
      <Typography variant="h4" gutterBottom>
        {form.name}
      </Typography>
      <Box component="form" onSubmit={handleSubmit}>
        {form.fields.map((field) => {
          const val = values[field.id];
          const err = errors[field.id] || '';
          switch (field.type) {
            case 'text':
            case 'number':
            case 'date':
              return (
                <Box key={field.id} mb={2}>
                  <TextField
                    fullWidth
                    type={field.type === 'number' ? 'number' : field.type}
                    label={field.label}
                    value={val}
                    onChange={(e) => handleChange(field.id, e.target.value)}
                    error={!!err}
                    helperText={err}
                  />
                </Box>
              );
            case 'textarea':
              return (
                <Box key={field.id} mb={2}>
                  <TextField
                    fullWidth
                    multiline
                    rows={4}
                    label={field.label}
                    value={val}
                    onChange={(e) => handleChange(field.id, e.target.value)}
                    error={!!err}
                    helperText={err}
                  />
                </Box>
              );
            case 'select':
              return (
                <Box key={field.id} mb={2}>
                  <FormControl fullWidth error={!!err}>
                    <InputLabel>{field.label}</InputLabel>
                    <Select
                      value={val}
                      onChange={(e) => handleChange(field.id, e.target.value)}
                    >
                      {field.options?.map((opt) => (
                        <MenuItem key={opt} value={opt}>
                          {opt}
                        </MenuItem>
                      ))}
                    </Select>
                    {err && <FormHelperText>{err}</FormHelperText>}
                  </FormControl>
                </Box>
              );
            case 'radio':
              return (
                <Box key={field.id} mb={2}>
                  <Typography>{field.label}</Typography>
                  <RadioGroup
                    value={val}
                    onChange={(e) => handleChange(field.id, e.target.value)}
                  >
                    {field.options?.map((opt) => (
                      <FormControlLabel key={opt} value={opt} control={<Radio />} label={opt} />
                    ))}
                  </RadioGroup>
                  {err && <FormHelperText error>{err}</FormHelperText>}
                </Box>
              );
            case 'checkbox':
              return (
                <Box key={field.id} mb={2}>
                  <FormControlLabel
                    control={
                      <Checkbox
                        checked={!!val}
                        onChange={(e) => handleChange(field.id, e.target.checked)}
                      />
                    }
                    label={field.label}
                  />
                  {err && <FormHelperText error>{err}</FormHelperText>}
                </Box>
              );
            default:
              return null;
          }
        })}
        <Box mt={2}>
          <Button type="submit" variant="contained">
            Submit
          </Button>
        </Box>
      </Box>
      <Snackbar
        open={alert.open}
        autoHideDuration={3000}
        onClose={() => setAlert((prev) => ({ ...prev, open: false }))}
      >
        <Alert severity={alert.severity} sx={{ width: '100%' }}>
          {alert.message}
        </Alert>
      </Snackbar>
    </Container>
  );
}
