// src/pages/MyFormsPage.tsx
import React, { useEffect } from 'react';
import { Container, Typography, List, ListItemButton, ListItemText } from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import { loadFormsFromLocalStorage } from '../store/formSlice';
import { RootState } from '../store/store';
import { useNavigate } from 'react-router-dom';

export default function MyFormsPage() {
  const { formsList } = useSelector((state: RootState) => state.form);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  useEffect(() => {
    dispatch(loadFormsFromLocalStorage());
  }, [dispatch]);

  return (
    <Container>
      <Typography variant="h4" gutterBottom>My Forms</Typography>
      <List>
        {formsList.map((form: { id: React.Key | null | undefined; name: any; createdAt: string | number | Date; }) => (
          <ListItemButton
            key={form.id}
            onClick={() => navigate(`/preview/${form.id}`)}
          >
            <ListItemText primary={form.name || '(unnamed)'} secondary={new Date(form.createdAt).toLocaleString()} />
          </ListItemButton>
        ))}
      </List>
    </Container>
  );
}
